import React,{  } from "react";
import Chatbot from 'react-simple-chatbot';

const SimpleForm = (props) => {
    return(
        <Chatbot
            steps={[
                {
                    id:'intro',
                    message:'Dear Human, answer a few questions to write your letter. We will post it for you',
                    trigger:'started',
                },
                {
                    id:'started',
                    options:[
                        {value:'start', label:'Get Started',trigger:'1'},
                        
                    ],
                },
                {
                    id:'1',
                    message:'Who would you like to send the letter to?',
                    trigger:'sender',
                },
                {
                    id:'sender',
                    options:[
                        {value:'lover', label:'a lover',trigger:'3'},
                        {value:'friend', label:'a friend far away',trigger:'3'},
                        {value:'stranger', label:'a stranger',trigger:'3'},
                    ],
                },
                {
                    id:'3',
                    message:'What his/her name',
                    trigger:'q-name',
                },
                {
                    id:'q-name',
                    user:true,
                    trigger:'5',
                },
                {
                    id:'5',
                    message:'What his/her address',
                    trigger:'q-address',
                },
                {
                    id:'q-address',
                    user:true,
                    trigger:'7',
                },
                {
                    id:'7',
                    message:'What type of letter you would like to write today?',
                    trigger:'q-lettertype',
                },
                {
                    id:'q-lettertype',
                    options:[
                        {value:'invitaion', label:'an invitation to collaborate', trigger:'9'},
                        {value:'love declaration', label:'a love declaration', trigger:'9'},
                        {value:'a peek', label:'a peek into the future', trigger:'9'},
                    ],
                },
                {
                    id:'9',
                    message:'Which of the following describes Yachika?',
                    trigger:'q-bestdescribe',
                },
                {
                    id:'q-bestdescribe',
                    options:[
                        {value:'brilliant', label:'infuriating but brilliant',trigger:'11'},
                        {value:'mysterious', label:'enigmating and mysterious',trigger:'11'},
                        {value:'rock', label:'solid as a rock',trigger:'11'},
                    ],
                
                },
                {
                    id:'11',
                    message:'What would you like letter lead to?',
                    trigger:'q-leadletter',
                },
                {
                    id:'q-leadletter',
                    options:[
                        {value:'tear', label:'bring a tear to Yachika eye',trigger:'13'},
                        {value:'differently', label:'Make Yachika see something differently',trigger:'13'},
                        {value:'project', label:'build a project together',trigger:'13'},
                    ],
                
                },
                {
                    id:'13',
                    message:'Anything else you want to say?',
                    trigger:'q-anymessage',
                },
                {
                    id:'q-anymessage',
                    user:true,
                    trigger:'15',
                },
                {
                    id:'15',
                    message:'How would you like to sign yourself:',
                    trigger:'signature',
                },
                {
                    id:'signature',
                    user:true,
                    trigger:'17',
                },
                {
                    id:'17',
                    message:'Thank you. Your letter is almost ready.',
                    end:true,
                },
            ]}/>
    )
}

export default SimpleForm;